<template>
  <div id="note">
    <head-top title="笔记"></head-top>

    <div class="search-input">
      <van-search placeholder="请输入搜索关键词" v-model="value" @search="onSearch" />
    </div>

    <van-pull-refresh v-model="isLoading" @refresh="getDataList" class="pull_fresh">
      <div class="main">
        <div class="item" v-for="(item, k) in searchData" :key="k" @click="showDetail(item)">
          <!-- <div class="item_operate">
          <van-icon name="delete" size="2rem" color="#f44" @click="dataDelete($event,item)" />
          </div>-->
          <div class="title">{{item['SNTtile']||'缺少标题'}}</div>
          <div class="content">
            <pre v-if="item['SNForm']===contentTypeDefault">{{shortContent(item['SNContent'])}}</pre>
            <img v-else :src="getImageFile(item['SNContent'])" alt />
          </div>
          <div class="time">{{item['CreateTime'] | formatDate(item['CreateTime']) }}</div>
        </div>
      </div>
    </van-pull-refresh>
  </div>
</template>

<script>
import headTop from "@/components/headTop";
import { Search, Icon } from "vant";
import { ContentType, ModulePicture } from "@/utils/enum";
export default {
  name: "",
  components: {
    [Search.name]: Search,
    [Icon.name]: Icon,
    headTop
  },
  props: {},
  data() {
    return {
      dataList: [],
      isLoading: false,
      form: {
        courseID: this.$store.getters.courseID,
        studentID: this.$store.getters.studentID,
        pno: 1,
        psize: 10
      },
      contentTypeDefault: ContentType.Text,
      value: ""
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      this.getDataList();
    });
  },
  methods: {
    getDataList() {
      this.isLoading = true;
      this.dataList = [];
      this.$mainHttp.note
        .getList(this.form)
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.dataList.push({
                ...item,
                img: this.$mainHttp.resourse.picture(
                  item["SNContent"],
                  ModulePicture.StudentNote
                )
              });
            });
          }
          this.isLoading = false;
        })
        .catch(err => {
          this.isLoading = false;
        });
    },
    showDetail(item) {
      this.$router.push({
        path: "/history/note/detail",
        query: {
          title: item["SNTtile"],
          type: item["SNForm"],
          content: item["SNContent"],
          img: item["SNID"]
        }
      });
    },
    shortContent(val) {
      if (val && val.length > 100) {
        return val.substring(0, 100) + "...";
      } else {
        return val;
      }
    },
    getImageFile(fileId) {
      return this.$mainHttp.resourse.picture(fileId, ModulePicture.StudentNote);
    },
    onSearch() {}
  },
  computed: {
    searchData() {
      if (!this.value) {
        return this.dataList;
      } else {
        return this.dataList.filter(
          r =>
            r["SNTtile"].indexOf(this.value) > -1 ||
            r["SNContent"].indexOf(this.value) > -1
        );
      }
    }
  },
  watch: {}
};
</script>

<style lang='less' scoped>
#note {
  .head-back {
    display: none;
  }
  .iconfanhui {
    position: absolute;
    left: 15px;
    top: 15px;
    color: #fff;
    font-size: 1.8rem;
  }
  .iconadd {
    float: right;
    margin-right: 4vw;
    line-height: 4.3rem;
    color: #fff;
    font-size: 1.8rem;
  }
  .search-input {
    .van-search__content {
      background: #ebf0ef;
      .van-cell__value--alone {
        font-size: 1.2rem;
      }
      .van-field__left-icon {
        margin-right: 5px;
        position: absolute;
        right: 10px;
      }
    }
  }
  .main {
    margin-top: 15px;
    width: 100vw;
    // height: 49vh;
    background: rgba(255, 255, 255, 1);
    height: calc(100vh - 4.5rem - 54px - 2vh);
    overflow-y: auto;
    .item {
      position: relative;
      overflow: hidden;
      width: 92vw;
      margin: 0 auto;
      text-align: left;
      border-bottom: 1px solid #dddddd;

      word-break: break-all;
      word-wrap: break-word;
      .item_operate {
        position: absolute;
        top: 15px;
        right: 0;
      }

      img {
        width: 160px;
        height: 113px;
      }
      .title {
        font-size: 1.4rem;
        color: rgba(51, 51, 51, 1);
        margin-top: 20px;
        margin-bottom: 15px;
      }
      .content {
        font-size: 1.2rem;
        color: rgba(102, 102, 102, 1);
        margin-bottom: 10px;

        pre {
          white-space: pre-wrap;
          white-space: -moz-pre-wrap;
          white-space: -pre-wrap;
          white-space: -o-pre-wrap;
        }
      }
      .time {
        font-size: 1.2rem;
        color: rgba(102, 102, 102, 1);
        margin-bottom: 20px;
      }
    }
  }

  .pull_fresh {
    /deep/ .van-pull-refresh__track {
      height: calc(100vh - 4.5rem - 54px - 2vh);
    }
  }
}
</style>


